.resultContainer {
    text-align: center;
}

.result {
    position: absolute;
    top:  50%;
    left: 50%;
    transform: translate(-50%,-50%);
}
.initResult{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 85%;
}
.initResult span{
    color: #808080;
    letter-spacing: 1px;
    font-size: larger;
}
.searchIcon {
    width: 60px;
}